import React, { Component } from "react";
import { Link } from "react-router-dom";
import moment from "moment";

/**UI component for Informed Consent */
class InformedConsent extends Component {
  /**Calls the action for saving consent if consent not given else navigates
   * to Questions
   */
  onProvidingConsent = () => {
    const {
      userProfile: { data }
    } = this.props;
    if (data.hasOwnProperty("consent") && data.consent.consentProvided) {
      this.props.history.push("/questions");
    } else {
      this.props.onSavingConsentRequest();
    }
  };
  render() {
    const {
      userProfile: { data },
      additionalInfo: { isLoading }
    } = this.props;
    return (
      <>
        <div className="emr_header">
          <div className="container">
            <div className="row">
              <div className="col-xs-12 col-sm-12 col-md-12">
                <h2 className="black"> Bailey Health of Pennsylvania P.C. </h2>
              </div>
              <div className="clearfix" />
              <ul className="tab_header">
                <li className="active1">
                  <i className="fa fa-check-circle" />
                  <Link to="/informed-consent"> Informed Consent </Link>
                </li>
                <li>
                  <Link to="/questions"> Questions </Link>
                </li>
                <li>
                  <Link to="/photos"> Photos </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>

        <div className="container">
          <div className="row">
            <div className="col-xs-12 col-sm-12 col-md-2"> </div>
            <div className="col-xs-12 col-sm-12 col-md-8">
              <h4>
                Bailey Health of Pennsylvania P.C. <br /> INFORMED CONSENT
                REGARDING USE OF TELEMEDICINE SERVICES
              </h4>
              <p>
                <strong>Patient's Legal Name</strong>{" "}
                {`${data.firstName} ${data.lastName}`}
              </p>
              {data.hasOwnProperty("shippingAddress") && (
                <p>
                  <strong>Location</strong> <br />{" "}
                  {data.shippingAddress[0].street},{" "}
                  {data.shippingAddress[0].city}
                  <br />
                  {data.shippingAddress[0].states} <br />
                  {data.shippingAddress[0].zipcode} <br />
                  USA
                </p>
              )}
              <p>
                <strong>Date of birth </strong>
                {data.dateOfBirth &&
                  new Date(
                    data.dateOfBirth.seconds * 1000
                  ).toLocaleDateString()}
              </p>
              <p>
                <strong>Date of Consent</strong>{" "}
                {(data.hasOwnProperty("consent") &&
                  moment(data.consent.dateOfConsent.seconds * 1000).format(
                    "LLL"
                  )) ||
                  moment().format("LLL")}
              </p>
              <strong> PURPOSE</strong> <br />
              <p>
                The purpose of this form is to provide you with information
                about telemedicine and to obtain your informed consent to
                participate in a telemedicine health service as part of your
                medical care.
              </p>
              <p>
                <strong>NATURE OF TELEMEDICINE </strong>
                <br />
                Telemedicine involves the use of electronic communications to
                enable a health care provider and a patient at different
                locations to share medical information for the purpose of
                evaluation, diagnosis, consultation, or treatment of the
                patient. The delivery of healthcare via telemedicine allows the
                patient and provider to establish a relationship, much as they
                would during a traditional face-to-face appointment. For
                example, your telemedicine encounter may include interaction
                through and with the use of the internet, recorded audio
                communications, physical examinations, medical imaging, medical
                tests, and diagnoses, as well as related technologies known as
                “store-and- forward.”
              </p>
              <p>
                <strong> BENEFITS</strong>
                <br />
                The benefits of telemedicine include improved access to medical
                services and care, including the expertise of specialists and
                consultants that may not otherwise be available to you.
                Telemedicine also permits increased efficiency in evaluations,
                diagnoses, consultations, and treatment.
              </p>
              <p>
                <strong> POTENTIAL RISKS</strong>
                <br /> The potential risks associated with the use of
                telemedicine are rare, but include delays in medical evaluation
                and treatment due to equipment failures or information
                transmission deficiencies (such as poor image resolution);
                breach of privacy of protected health information due to
                security breaches or failures; and adverse drug interactions,
                allergic reactions, complications, or other errors due to
                patient’s failure to provide complete medical information or
                records.
              </p>
              <p>
                <strong>INDEMNIFICATION</strong>
                <br />
                YOU AGREE TO INDEMNIFY AND HOLD HARMLESS Bailey Health of
                Pennsylvania P.C., ITS EMPLOYEES, AGENTS, DIRECTORS, MEMBERS,
                MANAGERS, SHAREHOLDERS, OFFICERS, REPRESENTATIVES, ASSIGNS,
                PARENTS, PREDECESSORS, AND SUCCESSORS FROM AND AGAINST ANY AND
                ALL LOSS, DAMAGE, EXPENSE, LIABILITY, CLAIM, OR DEMAND
                WHATSOEVER, ARISING OUT OF OR RELATED TO ANY FAILURE OF
                TECHNOLOGY OR EQUIPMENT IN CONNECTION WITH THE PROVISION OF
                TELEMEDICINE WHETHER OR NOT ANY SUCH LOSS, DAMAGE, EXPENSE,
                LIABILITY, CLAIM, OR DEMAND ARISES FROM OR RELATES TOBailey
                Health of Pennsylvania P.C.’S NEGLIGENCE.
              </p>
              <p>
                <strong>ALTERNATIVES</strong>
                <br />
                Alternative methods of care may be available to you, such as
                in-person services. Your provider will explain any such options
                to you, and you may choose an alternative at any time.
              </p>
              <p>
                <strong>FOLLOW-UP CARE; EMERGENCY SITUATIONS</strong>
                <br />
                In some situations, telemedicine is not an appropriate method of
                care. If there is an urgent situation, if you have an adverse
                reaction, if a technical failure prevents you from communicating
                with your telemedicine provider, or if you believe telemedicine
                will not provide sufficient safety and quality, you should
                contact Bailey Health of Pennsylvania P.C. as indicated below.
                If the contacts listed below are unavailable, you must seek care
                at an emergency room facility or other provider equipped to
                deliver urgent or emergent care. If the situation is an
                emergency, call 911.
              </p>
              <p>
                <strong>Phone: </strong>1-800-368-0038 (M-F 9AM – 5PM PT)
              </p>
              <p>
                <strong>YOUR PRIVACY RIGHTS</strong>
                <br />
                Bailey Health of Pennsylvania P.C. uses network and software
                security protocols to protect the confidentiality of your
                patient health information, including for example your medical
                record, EMR, imaging, and personal financial data. These
                protocols are designed to safeguard the data and to ensure its
                integrity against corruption. Personal information that
                identifies you or contains protected health information will not
                be disclosed to any third party without your consent, except as
                authorized by law for the purposes of consultation, treatment,
                payment/billing, and certain administrative purposes, or as
                otherwise set forth in Bailey Health’s Notice of Privacy
                Practices.
              </p>
              <p>
                <strong>
                  By signing this form, I understand the following
                </strong>
                <br />
                Telemedicine is the use of electronic information and
                communication technologies by a health care provider to deliver
                services to an individual when he/she is located at a different
                site than the provider. I understand that I need to provide a
                full and accurate medical history, including any pre-existing
                conditions, to my telemedicine provider so that my provider can
                accurately determine what services I need. I further understand
                that my provider will determine whether telemedicine is
                appropriate for me at this time, based on the condition being
                diagnosed and/or treated. I understand that I may benefit from
                telemedicine, but that results cannot be guaranteed. My provider
                will inform me who will be present at the provider’s location
                during the telemedicine service and I have the right to exclude
                anyone from being present, if I so choose. I further understand
                that I have the right to object to the use of a telemedicine
                service without prejudice to any future care or treatment and
                without risking the loss or withdrawal of any health benefits to
                which I am entitled. If there are costs to me associated with my
                telemedicine encounter, a health care professional will discuss
                those costs with me prior to the start of my session. Further, I
                understand and agree that I must pay the full amount of the
                costs associated with this telemedicine service, including any
                prescription I may receive, and I will not attempt to submit a
                claim to Medicare, any other federal payor, or any state or
                private insurer. I understand that the laws that protect privacy
                and the confidentiality of medical information also apply to
                telemedicine and I agree that Bailey Health may provide my
                confidential personal health information to other medical
                providers who may be located in other areas, including on rare
                occasions to providers outside the State, as necessary. I have
                the right to inspect and obtain copies of all information
                received and recorded during any telemedicine session, subject
                to the policies of the physicians, physician assistants, nurse
                practitioners and facilities involved in my care. I may be
                charged a fee for copies of my records in accordance with
                applicable State rules. I have read and understand the
                information above and all of my questions have been answered to
                my satisfaction.
              </p>
              <p>
                All photos are models and not actual patients unless expressed
                otherwise.
              </p>
              <p>
                If you have a concern about a medical professional, you may
                contact the Medical Board in your state regarding your concerns.
                For applicable contact information see the list available
                <Link to="#">here.</Link>
              </p>
              <p>
                I consent to a Bailey Health physician, physician assistant, or
                nurse practitioner to provide services to me via telemedicine.
              </p>
              <p>
                By clicking "I Agree" below, I understand and consent to the
                foregoing acknowledgements and disclosures including Bailey
                Health of Pennsylvania P.C. Terms of Service and Notice of
                Privacy Practices. Hims, Inc. has a commercial relationship with
                PostMeds and Curexa. You are free to obtain your prescription
                from any pharmacy of your choice. Hims, Inc. has a financial
                relationship with Bailey Health and the Provider. You are free
                to obtain your medical examination from another healthcare
                provider that is not associated with Hims, Inc. Hims, Inc. will
                use its pharmacy partner to fulfill your order directly to your
                door. You are free to obtain your prescription from any pharmacy
                of your choice by contacting our support team. Further, for
                purposes of this informed consent, MY ACT OF CLICKING "I Agree"
                SHALL CONSTITUTE AND IS MY ELECTRONIC SIGNATURE.
              </p>
              <p>
                <button
                  className="consent_next_btn"
                  onClick={this.onProvidingConsent}
                >
                  {data.hasOwnProperty("consent") &&
                  data.consent.consentProvided
                    ? isLoading
                      ? "Saving Consent..."
                      : "Consent provided - Next"
                    : "Provide Consent"}
                </button>
              </p>
              <p align="center">
                Powered by Hims <br />
                <Link to="#" className="link">
                  Terms and Conditions
                </Link>
              </p>
            </div>
            <div className="col-xs-12 col-sm-12 col-md-2"> </div>
          </div>
        </div>
      </>
    );
  }
}

export default InformedConsent;
